.edit-component-icon {
    display: none;
    cursor: pointer;
    color: var(--color-primary);
    width: 20px;
    height: 20px;
    margin-right: 8px;
    z-index: 99;
}

.edit-component-icon svg {
    width: 16px;
    height: 16px;
    border: 1px solid var(--color-primary);
    border-radius: 50%;
    padding: 2px;
}

.drag-handle {
    display: none;
    cursor: pointer;
    color: var(--color-primary);
    width: 20px;
    height: 20px;
    margin-right: 8px;
    z-index: 99;
}

.drag-handle svg {
    width: 16px;
    height: 16px;
    border: 1px solid var(--color-primary);
    border-radius: 50%;
    padding: 2px;
}

.copy-component {
    display: none;
    cursor: pointer;
    color: var(--color-primary);
    width: 20px;
    height: 20px;
    margin-right: 8px;
    z-index: 99;
}

.copy-component svg {
    width: 16px;
    height: 16px;
    border: 1px solid var(--color-primary);
    border-radius: 50%;
    padding: 2px;
}

.remove-component {
    display: none;
    cursor: pointer;
    color: var(--color-primary);
    width: 20px;
    height: 20px;
    margin-right: 8px;
    z-index: 99;
}

.remove-component svg {
    width: 16px;
    height: 16px;
    border: 1px solid var(--color-primary);
    border-radius: 50%;
    padding: 2px;
}

.delete-component {
    display: none;
    cursor: pointer;
    color: var(--color-error);
    width: 20px;
    height: 20px;
    margin-right: 8px;
    z-index: 99;
}

.delete-component svg {
    width: 16px;
    height: 16px;
    border: 1px solid var(--color-error);;
    border-radius: 50%;
    padding: 2px;
}

.resume-section-wrap:hover .drag-handle,
.component-dragging .drag-handle,
.resume-section-wrap:hover .copy-component,
.resume-section-wrap:hover .delete-component,
.resume-section-wrap:hover .remove-component {
    display: block;
}

.resume-paper-content-draggin-over {
    border: 1px dashed var(--color-primary);
    background-color: #f7fffc94;
    border-radius: 4px;
    min-height: 200px;
}

